"use client";

import { motion } from "framer-motion";

export default function AnimatedBackground() {
  return (
    <div className="fixed inset-0 z-0 pointer-events-none overflow-hidden">
      <motion.div
        className="absolute inset-0 opacity-30"
        style={{
          backgroundImage: "radial-gradient(#4a4a4a 1px, transparent 1px)",
          backgroundSize: "30px 30px",
          willChange: "transform",
        }}
        initial={{ y: 0, scale: 1 }}
        animate={{ y: [-12, 12, -12], scale: [1, 1.01, 1] }}
        transition={{ duration: 18, repeat: Infinity, ease: "easeInOut" }}
      />

      <motion.div
        className="absolute inset-0"
        style={{
          backgroundImage:
            "repeating-linear-gradient(0deg, rgba(255,255,255,0.03) 0px, rgba(255,255,255,0.03) 1px, rgba(0,0,0,0) 2px)",
        }}
        initial={{ opacity: 0.1 }}
        animate={{ opacity: [0.1, 0.18, 0.1] }}
        transition={{ duration: 10, repeat: Infinity, ease: "easeInOut" }}
      />

      <motion.div
        className="absolute inset-0"
        style={{
          background: "radial-gradient(ellipse at center, rgba(16,16,16,0.0) 0%, rgba(0,0,0,0.5) 60%)",
          filter: "blur(4px)",
        }}
        initial={{ opacity: 0.2, scale: 1 }}
        animate={{ opacity: [0.28, 0.4, 0.28], scale: [1, 1.02, 1] }}
        transition={{ duration: 16, repeat: Infinity, ease: "easeInOut" }}
      />

      <motion.div
        className="absolute top-0 h-full w-24"
        style={{
          background:
            "linear-gradient(90deg, rgba(0,0,0,0) 0%, rgba(0,255,128,0.08) 50%, rgba(0,0,0,0) 100%)",
          filter: "blur(8px)",
          opacity: 0.35,
        }}
        initial={{ x: "-10%" }}
        animate={{ x: ["-10%", "110%"] }}
        transition={{ duration: 22, repeat: Infinity, ease: [0.22, 1, 0.36, 1] }}
      />

      <motion.div
        className="absolute -top-24 -left-24 w-[40rem] h-[40rem] rounded-full blur-3xl"
        style={{
          background:
            "radial-gradient(circle, rgba(0,255,128,0.22) 0%, rgba(0,255,128,0.0) 60%)",
          mixBlendMode: "screen",
        }}
        initial={{ opacity: 0.25, x: 0, y: 0, scale: 1 }}
        animate={{ opacity: [0.25, 0.35, 0.25], x: [0, 50, -25, 0], y: [0, 24, -36, 0], scale: [1, 1.02, 1] }}
        transition={{ duration: 30, repeat: Infinity, ease: "easeInOut" }}
      />

      <motion.div
        className="absolute -bottom-24 -right-24 w-[40rem] h-[40rem] rounded-full blur-3xl"
        style={{
          background:
            "radial-gradient(circle, rgba(0,255,128,0.18) 0%, rgba(0,255,128,0.0) 60%)",
          mixBlendMode: "screen",
        }}
        initial={{ opacity: 0.22, x: 0, y: 0, scale: 1 }}
        animate={{ opacity: [0.22, 0.32, 0.22], x: [0, -40, 24, 0], y: [0, -24, 36, 0], scale: [1, 1.015, 1] }}
        transition={{ duration: 28, repeat: Infinity, ease: "easeInOut" }}
      />

      <motion.div
        className="absolute inset-0"
        style={{
          backgroundImage:
            "repeating-linear-gradient(135deg, rgba(0,255,128,0.06) 0px, rgba(0,255,128,0.06) 2px, rgba(0,0,0,0) 8px)",
          filter: "blur(1px)",
        }}
        initial={{ opacity: 0.06 }}
        animate={{ opacity: [0.06, 0.1, 0.06] }}
        transition={{ duration: 18, repeat: Infinity, ease: "easeInOut" }}
      />
    </div>
  );
}
